import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { SET_DOCUMENTS } from "../../../../utilities/constants";
import { imageUploader } from "../../../../utilities/fileHandlers";
import $ from "jquery";
export default function EditDocuments(props) {
  const [images, setImages] = useState([]);
  const [document, setDocument] = useState([]);
  const [restaurant_image, setRestaurantImage] = useState("");
  const [banner_image, setBannerImage] = useState("");
  const [dynamicImageField, setDynamicImageField] = useState([
    {
      image_name: "",
    },
  ]);
  const selector = useSelector((state) => state.restaurant);
  const { documents } = selector;
  useEffect(() => {
    setDocument(documents);
  }, [documents]);
  const dispatch = useDispatch();

  const handleImageAdded = () => {
    setDynamicImageField([...dynamicImageField, { image_name: "" }]);
  };
  const handleRemoveImage = (index) => {
    const values = [...dynamicImageField];
    values.splice(index, 1);
    setDynamicImageField(values);
  };

  const handleContinue = (e) => {
    const data = {
      ...selector,
      documents: [...images],
    };
    dispatch({
      type: SET_DOCUMENTS,
      payload: data,
    });
    props.goToStep(3);
  };
  const handleChangeInput = (index, event) => {
    const value = [...dynamicImageField];
    value[index][event.target.name] = event.target.value;
    setDynamicImageField(value);
  };
  const handleDynamicFiles = (index, event) => {
    const value = [...dynamicImageField];
    let file = event.target.files[0];
    imageUploader(file, (result) => {
      value[index][event.target.name] = result;
      setDynamicImageField(value);
    });
    setImages([...images, dynamicImageField]);
  };
  const handleBack = (e) => {
    e.preventDefault();
    props.goToStep(1);
  };
  const onImageChange = (e) => {
    let file = e.target.files[0];
    let name = e.target.name;
    name === "restaurant_image"
      ? setRestaurantImage(file.name)
      : setBannerImage(file.name);
    imageUploader(file, (result) => {
      let image = {};
      image[name] = result;
      setImages([...images, image]);
      var parentTag = $("img#" + name).get();
      $(parentTag).attr("src", result);
    });
  };

  return (
    <fieldset>
      <div className="row mt-2">
        {document &&
          document.map((data, key) => (
            <div className="col-lg-4" key={key}>
              <div className="editable-image-container">
                <img
                  loading="lazy"
                  src={Object.values(data)}
                  alt="image_name"
                  id={Object.keys(data)}
                  className="img-thumbnail editable-image"
                />
                <div className="editable-middle">
                  <label
                    htmlFor="upload"
                    className="change-image-caption text-white"
                    data-toggle="tooltip"
                    title="Change Image"
                  >
                    <span aria-hidden="true">
                      <i className="fa fa-camera" />
                      <input
                        type="file"
                        id="file"
                        style={{ opacity: 0 }}
                        name={Object.keys(data)}
                        onChange={(event) => onImageChange(event)}
                      />
                    </span>
                  </label>
                </div>
              </div>
            </div>
          ))}
      </div>

      {dynamicImageField.map((inputFields, index) => (
        <div className="row mt-2" key={index}>
          <div className="col-lg-5">
            <div className="form-group">
              <label>Image Name</label>
              <input
                type="text"
                className="form-control"
                name="image_name"
                value={inputFields.image_name}
                onChange={(event) => handleChangeInput(index, event)}
              />
            </div>
          </div>
          <div className="col-lg-5">
            <div className="form-group">
              <label>Image</label>
              <div className="custom-file">
                <input
                  id="logo"
                  type="file"
                  name={"image_".concat(index)}
                  className="custom-file-input"
                  onChange={(event) => handleDynamicFiles(index, event)}
                />
                <label htmlFor="logo" className="custom-file-label">
                  {inputFields.image_name}
                </label>
              </div>
            </div>
          </div>
          <div className="col-lg-2">
            <div className="form-inline mt-4 py-2">
              <button
                className="btn btn-danger mr-2"
                type="button"
                onClick={() => handleRemoveImage(index)}
              >
                <i className="fa fa-trash-o" />
              </button>
              <button
                className="btn btn-primary"
                type="button"
                onClick={(event) => handleImageAdded(event)}
              >
                <i className="fa fa-plus" />
              </button>
            </div>
          </div>
        </div>
      ))}
      <div className="row mt-2">
        <div className="col-lg-12 justify-content-end">
          <button
            type="button"
            className="btn btn-primary float-right mr-2 "
            onClick={handleContinue}
          >
            Next
          </button>
          <button
            type="button"
            className="btn btn-default float-right mr-2 "
            onClick={handleBack}
          >
            Previous
          </button>
        </div>
      </div>
    </fieldset>
  );
}
