import React, { useState } from "react";
import EditDynamicMeal from "./EditDynamicMeal";
import { Tabs, Tab } from "react-bootstrap";
import {useDispatch,useSelector} from 'react-redux'
import { SET_MEALS } from "../../../../utilities/constants";

export default function EditMeals(props) {
  const [key, setKey] = useState("monday");
  const [meals, setMeals] = useState([]);
  const state = useSelector(state => state.restaurant)
  const dispatch = useDispatch()
  const handleContinue = (e) => {
    const data = {
      ...state,
      meals: [...meals]
    }
    dispatch({
      type: SET_MEALS,
      payload:data
    })
    props.goToStep(4);
  };
  const handleBack = (e) => {
    e.preventDefault();
    props.goToStep(2);
  };
  const addMeal = (index, meal) => {
    setMeals([...meals, meal]);
  };
  return (
    <fieldset>
      <Tabs
        className="tabs-container mt-1"
        activeKey={key}
        onSelect={(k) => setKey(k)}
      >
        <Tab eventKey="monday" title="Monday">
          <EditDynamicMeal index={1} addMeal={addMeal} />
        </Tab>
        <Tab eventKey="tuesday" title="Tuesday">
          <EditDynamicMeal index={2} addMeal={addMeal} />
        </Tab>
        <Tab eventKey="wednesday" title="Wednesday">
          <EditDynamicMeal index={3} addMeal={addMeal} />
        </Tab>
        <Tab eventKey="thursday" title="Thursday">
          <EditDynamicMeal index={4} addMeal={addMeal} />
        </Tab>
        <Tab eventKey="friday" title="Friday">
          <EditDynamicMeal index={5} addMeal={addMeal} />
        </Tab>
        <Tab eventKey="saturday" title="Saturday">
          <EditDynamicMeal index={6} addMeal={addMeal} />
        </Tab>
        <Tab eventKey="sunday" title="Sunday">
          <EditDynamicMeal index={7} addMeal={addMeal} />
        </Tab>
      </Tabs>

      <div className="col-lg-12 justify-content-end">
        <button
          type="button"
          className="btn btn-primary float-right mr-2 "
          onClick={handleContinue}
        >
          Next
        </button>
        <button
          type="button"
          className="btn btn-default float-right mr-2 "
          onClick={handleBack}
        >
          Previous
        </button>
      </div>
    </fieldset>
  );
}
