import React from "react";

export default function SlotPicker({ slot }) {
  return (
    <>
      <div className="ibox">
        <div className="ibox-heading">
          <h3 className="ibox-title">Current Slots</h3>
        </div>
        <div className="ibox-content">
          <p>
            <strong>Slot 1:</strong> 11-12PM
          </p>
          <hr className="hr-line-solid" />
          <p>
            <strong>Slot 2:</strong> 12-1PM
          </p>
          <hr className="hr-line-solid" />
          <p>
            <strong>Slot 3:</strong> 1-2PM
          </p>
          <hr className="hr-line-solid" />
        </div>
      </div>
      <div className="row">
        <div className="col-lg-8">
          <div className="form-group">
            <label>
              Select a time <strong className="text-danger">*</strong>
            </label>
            <select
              name="delivery_slot"
              defaultValue="Select a Slot"
              className="form-control required"
            >
              {slot.map((data, key) => (
                <option name={data.time} value={data.time} key={key}>
                  {data.time}
                </option>
              ))}
            </select>
          </div>
        </div>
        <div className="col-lg-4">
          <button type="button" className="btn btn-primary mt-4">
            Next
          </button>
        </div>
      </div>
    </>
  );
}
