import axios from "axios";
import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { BASIC_INFO } from "../../../../utilities/constants";

export default function EditBasicInfo(props) {
  const restaurant = useSelector((state) => state.restaurant);
  const [cuisine, setCuisine] = useState([]);
  useEffect(() => {
    let componentMounted = true;
    const fetchData = async () => {
      const response = await axios.get(
        "/api/cuisine/"
      );
      const { data } = await response;
      if (componentMounted) {
        setCuisine(data);
      }
    };
    fetchData();
    return () => {
      componentMounted = false;
    };
  });
  const dispatch = useDispatch();
  const [state, setState] = useState({});

  const onChangeText = ({ target }) => {
    setState((prevState) => ({ ...prevState, [target.name]: target.value }));
  };

  const handleContinue = (e) => {
    dispatch({ type: BASIC_INFO, payload: state });
    props.goToStep(2);
  };

  return (
    <fieldset>
      <div className="row mt-2">
        <div className="col-lg-4">
          <div className="form-group">
            <label>
              RESTAURANT NAME <strong className="text-danger">*</strong>
            </label>
            <input
              name="restaurant_name"
              defaultValue={restaurant.restaurant_name}
              onChange={(e) => onChangeText(e)}
              type="text"
              className="form-control required"
            />
          </div>
          <div className="form-group">
            <label>
              OWNER NAME <strong className="text-danger">*</strong>
            </label>
            <input
              name="owner_name"
              defaultValue={restaurant.owner_name}
              onChange={(e) => onChangeText(e)}
              type="text"
              className="form-control required"
            />
          </div>
          <div className="form-group">
            <label>
              STATE <strong className="text-danger">*</strong>
            </label>
            <input
              name="state"
              defaultValue={restaurant.state}
              onChange={(e) => onChangeText(e)}
              type="text"
              className="form-control required"
            />
          </div>
          <div className="form-group">
            <label>
              CUISINE <strong className="text-danger">*</strong>
            </label>
            <select
              name="cuisine_type"
              onChange={(e) => onChangeText(e)}
              defaultValue={restaurant.cuisine_type}
              className="form-control required"
            >
              {cuisine.map((data, key) => (
                <option
                  name={data.cuisineName}
                  value={data.cuisineName}
                  key={key}
                >
                  {data.cuisineName}
                </option>
              ))}
            </select>
          </div>
        </div>
        <div className="col-lg-4">
          <div className="form-group">
            <label>
              PHONE NUMBER <strong className="text-danger">*</strong>
            </label>
            <input
              name="phone"
              defaultValue={restaurant.phone}
              onChange={(e) => onChangeText(e)}
              type="text"
              className="form-control required"
            />
          </div>
          <div className="form-group">
            <label>
              STREET <strong className="text-danger">*</strong>
            </label>
            <input
              name="locality"
              defaultValue={restaurant.locality}
              onChange={(e) => onChangeText(e)}
              type="text"
              className="form-control required"
            />
          </div>
          <div className="form-group">
            <label>
              COUNTRY <strong className="text-danger">*</strong>
            </label>
            <input
              name="country"
              defaultValue={restaurant.country}
              onChange={(e) => onChangeText(e)}
              type="text"
              className="form-control required"
            />
          </div>
          <div className="form-group">
            <label>
              COMMISSION <strong className="text-danger">*</strong>
            </label>
            <input
              name="commission"
              defaultValue={restaurant.commission}
              onChange={(e) => onChangeText(e)}
              type="text"
              className="form-control required"
            />
          </div>
        </div>
        <div className="col-lg-4">
          <div className="form-group">
            <label>
              EMAIL ADDRESS <strong className="text-danger">*</strong>
            </label>
            <input
              name="email"
              defaultValue={restaurant.email}
              onChange={(e) => onChangeText(e)}
              type="text"
              className="form-control required"
            />
          </div>
          <div className="form-group">
            <label>
              CITY <strong className="text-danger">*</strong>
            </label>
            <input
              name="city"
              defaultValue={restaurant.city}
              onChange={(e) => onChangeText(e)}
              type="text"
              className="form-control required"
            />
          </div>
          <div className="form-group">
            <label>
              POSTAL CODE <strong className="text-danger">*</strong>
            </label>
            <input
              name="postal_code"
              defaultValue={restaurant.postal_code}
              onChange={(e) => onChangeText(e)}
              type="text"
              className="form-control required"
            />
          </div>
          <div className="form-group">
            <label>
              Meal Type <strong className="text-danger">*</strong>
            </label>
            <select
              className="form-control required"
              name="meal_type"
              onChange={(e) => onChangeText(e)}
              defaultValue={restaurant.meal_type}
            >
              <option value="veg">Veg</option>
              <option value="non-veg">Non-Veg</option>
            </select>
          </div>
        </div>
      </div>
      <div className="row mt-2">
        <div className="col-lg-4">
          <div className="form-group">
            <label>
              MEAL TIME <strong className="text-danger">*</strong>
            </label>
            <select
              className="form-control required"
              name="category"
              defaultValue={restaurant.category}
              onChange={(e) => onChangeText(e)}
            >
              <option value="Lunch">Lunch</option>
              <option value="Dinner">Dinner</option>
            </select>
          </div>
        </div>
        <div className="col-lg-8">
          <div className="form-group">
            <label>
              About <strong className="text-danger">*</strong>
            </label>
            <textarea
              className="form-control form-control-plaintext"
              name="about"
              onChange={(e) => onChangeText(e)}
              defaultValue={restaurant.about}
            />
          </div>
        </div>
      </div>
      <div className="row">
        <div className="col-lg-12 justify-content-end">
          <button
            type="button"
            className="btn btn-primary float-right mr-2"
            onClick={handleContinue}
          >
            Next
          </button>
        </div>
      </div>
    </fieldset>
  );
}
