import React from "react";
import { useSelector } from "react-redux";

export default function Meals(props) {
  const restaurant = useSelector((state) => state.restaurant);
  const { meals } = restaurant;
  return (
    <div className="row mt-2">
      {meals &&
        meals.map((data, key) => (
          <div className="col-md-4" key={key}>
            <div className="ibox">
              <div className="ibox-content product-box active">
                <img className="img-thumbnail" src={data.image} alt="Meal" />
                <div className="product-desc">
                  <span className="product-price">{data.day}</span>
                  <div className="row justify-content-between ml-1">
                    <small className="text-muted text-capitalize">
                      {data.type}
                    </small>
                    <small className="text-muted text-capitalize">
                      {data.slot}
                    </small>
                  </div>
                  <a href="/" className="product-name">
                    {data.meal_name}
                  </a>
                  <div className="small m-t-xs">{data.description}</div>
                  <hr className="hr-line-dashed" />
                  <div className="m-t text-left">
                    {data.add_on.length >1 &&
                      data.add_on.map((addOn, key) => (
                        <>
                          <h5>Add Ons</h5>
                          <div className="row-sm">
                            <small className=" col-sm-4 text-muted text-capitalize">
                              {addOn.add_on}
                            </small>
                            <small className="col-sm-4 text-muted text-capitalize">
                              {addOn.add_on_price}
                            </small>
                            <img
                              className="col-sm-4 img-sm"
                              src={addOn.add_on_image}
                              alt="Add On"
                            />
                          </div>
                        </>
                      ))}
                  </div>
                  <div className="m-t text-right">
                    <a href="/" className="btn btn-xs btn-outline btn-primary">
                      Info <i className="fa fa-long-arrow-right" />{" "}
                    </a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        ))}
    </div>
  );
}
